import { Component, h, Host, Prop, Element, State, Watch } from '@stencil/core';
import ValidateInput from '../../input/validate';

@Component({
    tag: 'ks-page-register',
    styleUrl: 'register.css'
})
export class PageRegister {
    @Element() root: HTMLElement;
    
    @Prop() heading: string = "Rejestracja";
    @Prop() headingLoginData: string = "Dane logowania";
    @Prop() headingUserData: string = "Dane użytkownika";
    @Prop() headingPermissionData: string = "Zgody";

    @Prop() newsletter: string = 'Wyrażam zgodę na otrzymywanie newslettera na podany adres e-mail. Wyrażam zgodę na przetwarzanie moich danych osobowych przez Insparo Patrycja Ordyk z siedzibą w Jeleniej Górze (58-500) przy ul. Sobieskiego 47 Hala E. <strong>Za dołączenie do newslettera otrzymasz kupon 10 zł!</strong>';
    @Prop() terms: string = 'Oświadczam, że zapoznałem/am się z <a href="https://kuchniasklep.pl/regulamin-sklepu-internetowego-pm-113.html" target="_blank"><strong>regulaminem</strong></a> i <a href="https://kuchniasklep.pl/polityka-prywatnosci-pm-54.html" target="_blank"><strong>polityką prywatności</strong></a> oraz akceptuję ich warunki.<br> <br> Administratorem danych osobowych jest Insparo Patrycja Ordyk z siedzibą w Jeleniej Górze (58-500) przy ul. Sobieskiego 47 Hala E, e-mail: sklep@kuchniasklep.pl (Administrator) W Polityce Prywatności Administrator informuje o celu, okresie i podstawach prawnych przetwarzania danych osobowych, a także o prawach jakie przysługują osobom, których przetwarzane dane osobowe dotyczą, podmiotom którym Administrator może powierzyć do przetwarzania dane osobowe, oraz o zasadach zautomatyzowanego przetwarzania danych osobowych, w tym ich profilowaniu. Wyrażam zgodę na przetwarzanie przez Sprzedawcę moich danych osobowych zawartych w formularzu rejestracji konta w celu i zakresie niezbędnym do realizacji zamówień.';
    @Prop() register: string = "Zarejestruj się";

    @Prop() ip: string;
    @Prop() host: string;
    
    @Prop() accountLink: string;
    @Prop() registerApi: string = "";
    @Prop() emailApi: string = "res/true?email=";
    @Prop() emailError: string = "Podany email został już wykorzystany, skorzystaj z funkcji odzyskiwania hasła.";
    @Prop() postcodeError: string = "Wprowadź kod pocztowy w formacie XX-XXX";
    @Prop() streetError: string = "Wprowadź conajmniej nazwę ulicy oraz numer domu";
    @Prop() phoneError: string = "Wprowadź prawidłowy numer telefonu";
    
    @Prop() success_heading: string = "Zarejestrowano";
    @Prop() success_message: string = "Twoje konto zostało założone, za chwilę nastąpi przekierowanie na panel klienta.";

    @Prop() failure_heading: string = "Nie zarejestrowano";
    @Prop() failure_message: string = "Sprawdź wprowadzone dane i spróbuj ponownie, jeżeli problem dalej występuje skontaktuj się z naszą obsługą.";
    @Prop() notification_button: string = "Wróć do rejestracji";
    
    @State() loading: boolean = false;
    @State() company: boolean = false;
    @State() notification_heading: string;
    @State() notification_message: string;

    @State() country: string = "170";
    @Watch("country") async countryWatcher() {
        setTimeout(() => {
            (this.root.querySelector("#postcode_input") as HTMLKsInputTextElement).Validate();
        }, 100);
    }

	render() {
        const postcodeRegex = this.country == "170" ? "^[0-9]{2}-[0-9]{3}$" : undefined;

		return <Host>
            <section class="container">
                <h1>{this.heading}</h1>

                <form id="register" enctype="application/x-www-form-urlencoded" onSubmit={(event)=>this.submit_register(event)}>
                    <h2>{this.headingLoginData}</h2>
                    <ks-input-dynamic api={this.emailApi} message={this.emailError}>
                        <ks-input-text email required name='email' label="Adres email" icon="mail" />
                    </ks-input-dynamic>

                    <ks-input-text password required name='haslo' label="Hasło" icon="key" min={8} max={128} passwordCharacters />
                    <ks-input-text password required name='haslopowtorz' label="Powtórz hasło" icon="key" sameAs="haslo" />

                    <h2>{this.headingUserData}</h2>
                    <div class="radio">
                        <ks-input-check radio name="osobowosc" value="1" label="Osoba fizyczna" checked onChange={() => this.company = false} />
                        <ks-input-check radio name="osobowosc" value="0" label="Firma" onChange={() => this.company = true}/>
                    </div>

                    {this.company && <div class="columns">
                        <ks-input-text required name='nazwa_firmy' label="Nazwa Firmy" icon="briefcase" />
                        <ks-input-text required name='nip_firmy' label="Numer NIP" icon="briefcase" />
                    </div>}

                    <ks-input-select name='panstwo' label="Kraj" onChange={e => this.country = (e.target as HTMLSelectElement).value}>
                        <slot name="country" />
                    </ks-input-select>

                    <div class="columns">
                        <div>
                            <ks-input-text required name='imie' label="Imię" icon="user" />
                            <ks-input-text required name='nazwisko' label="Nazwisko" icon="user" />
                            <ks-input-text required phone name='telefon' label="Numer Telefonu" icon="phone" />
                        </div>

                        <div>
                            <ks-input-text required name='miasto' label="Miasto" icon="map-pin" />
                            <ks-input-text required name='ulica' label="Ulica i numer domu / lokalu" icon="map-pin" regex="[a-zа-я].*[0-9]|[0-9].*[a-zа-я]" regexMessage={this.streetError}/>
                            <ks-input-text required id="postcode_input" name='kod_pocztowy' label="Kod Pocztowy" icon="map-pin" regex={postcodeRegex} regexMessage={this.postcodeError} />
                        </div>
                    </div>


                    <h2>{this.headingPermissionData}</h2>

                    <ks-input-check name="biuletyn" label={this.newsletter} />
                    <ks-input-check required name="regulamin" label={this.terms} />

                    <input type="hidden" name="adres_ip" value={this.ip} />
                    <input type="hidden" name="nazwa_hosta" value={this.host} />
                    <input type="hidden" name="akcja" value="zapisz" />

                    <ks-button rounded primary tall submit name={this.register} loading={this.loading} class="register" />
                </form>

                <ks-dialog id="notification-dialog" dark>
                    <section>
                        <h1>{this.notification_heading}</h1>
                        <p>{this.notification_message}</p>

                        <ks-button rounded onClick={()=>this.hide_notification()} name={this.notification_button} />
                    </section>
                </ks-dialog>

                <ks-overlay dark id="loading-overlay" close={false}>
                    <div class="success">
                        <h1>{this.success_heading}</h1>
                        <p>{this.success_message}</p>
                        <div class="loader">
                            <ks-loader large />
                        </div>
                    </div>
                </ks-overlay>
            </section>
        </Host>;
	}

    redirect_to_account() {
        const dialog = this.root.querySelector("#loading-overlay") as any;
        dialog.show();

        setTimeout(() => {
            window.location.href = this.accountLink;
        }, 1000);
    }

    show_notification(heading: string, message: string) {
        this.notification_heading = heading;
        this.notification_message = message;

        const dialog = this.root.querySelector("#notification-dialog") as any;
        dialog.show();
    }

    hide_notification() {
        const dialog = this.root.querySelector("#notification-dialog") as any;
        dialog.hide();
    }

    async submit_register(event: Event) {
        event.preventDefault();
        this.loading = true;

        const form = this.root.querySelector("#register") as HTMLFormElement;

        if(!await ValidateInput(form)) {
            this.loading = false;
            return;
        }

        const filteredParams = new URLSearchParams();

        new FormData(form).forEach((value, key) => {
            filteredParams.append(key, value.toString());
        })

        const formData = new FormData();
        formData.append("data", filteredParams.toString());

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        await fetch(this.registerApi, {
            body: formData,
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        })
        .then(response => {
            if(!response.ok) throw response.status;
            return response.text();
        })
        .then(response => {
            if(response.includes("Twoje konto zostało założone") || true)
                this.redirect_to_account();
            //else this.show_notification(this.failure_heading, this.failure_message);
        })
        .catch(error => {
            console.log(error);
            let message = error;

            if(!navigator.onLine)
                message = "Brak połączenia z internetem.";

            else if(Number.isInteger(error))
                message = `Błąd połączenia - ${error}`;

            this.show_notification(this.failure_heading, message);
        });

        this.loading = false;
    }
}
