ks-page-register {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    
}

ks-page-register .container {
    max-width: 100%;
    width: 1000px;
    box-sizing: border-box;
    padding: 30px;
    margin: 30px 0px;
    background-color: #fff;
    border-radius: 10px;
}

ks-page-register .container > h1 {
    text-align: center;
    margin: 10px 0px 5px 0px;
}

ks-page-register form > h2 {
    margin-top: 30px;
}

ks-page-register form > ks-button {
    margin-top: 40px;
}


ks-page-register .columns {
    display: flex;
    gap: 20px;
    margin-bottom: 15px;
}

ks-page-register .columns > * {
    width: 100%;
}

ks-page-register .radio {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

ks-page-register .success {
    text-align: center;
}

ks-page-register .success .loader {
    position: relative;
    width: 100px;
    height: 100px;
    margin: auto;
}

ks-page-register ks-dialog .content {
    width: 100%;
}

ks-page-register #notification-dialog section ks-button {
    margin-top: 20px;
}

@media (max-width: 1060px) {
    ks-page-register {
        background-color: #fff;
    }

    ks-page-register .container {
        padding: 20px;
        margin: 0px;
    }
}

@media (max-width: 960px) {
    ks-page-register .columns {
        flex-direction: column;
        gap: 20px;
    }
}